.Feature, .has-subfeatures, .no-subfeatures {
  width: 300px;
  min-height: 50px;
}

.has-subfeatures, .no-subfeatures {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}

.has-subfeatures span, .no-subfeatures span {
  margin-left: 10px;
}

.Feature {
  font-size: 16px;
  list-style-type: none;
  margin: 5px 5px;
  border: 1px solid #666363;
  -webkit-box-shadow: 2px 2px rgba(116, 113, 113, 0.445);
          box-shadow: 2px 2px rgba(116, 113, 113, 0.445);
  border-radius: 5px;
}

.has-subfeatures:hover {
  background-color: #40d9ac;
  -webkit-transition: background-color 0.1s linear;
  -o-transition: background-color 0.1s linear;
  transition: background-color 0.1s linear;
  cursor: pointer;
}

.has-subfeatures:hover .show-subfeatures-text {
  display: inline;
  -webkit-transition: display 0.2s linear;
  -o-transition: display 0.2s linear;
  transition: display 0.2s linear;
}

.show-subfeatures-text {
  opacity: 0.5;
  font-size: 12px;
  display: none;
}

.icon-right {
  font-size: 24px;
  margin-right: 10px;
}

.green {
  color: #06ba63;
}

.red {
  color: #f33939;
}



